
import React, { useState } from 'react'

import styles from './CCvsYou.module.css';

import { VerticalBar } from './VerticalBar';

const CC_COLOR = '#059bde';
const YOU_COLOR = '#1ac1b8';


export const CCvsYou = (props) => {
  const { data, translations } = props;

  const [selectedTime, setSelectedTime] = useState('day');

  let userValue;
  let ccValue;
  if (selectedTime === 'day') {
    userValue = data.day;
    ccValue = data.ccDay;
  } else if (selectedTime === 'week') {
    userValue = data.week;
    ccValue = data.ccWeek;
  } else if (selectedTime === 'month') {
    userValue = data.month;
    ccValue = data.ccMonth;
  }

  let userFirst = true;
  if (ccValue > userValue) {
    userFirst = false;
  }

  const maxSteps = userFirst ? userValue : ccValue;

  const ccBar = (
    <VerticalBar
      key={'cc' + selectedTime}
      mainColor={CC_COLOR}
      maxSteps={maxSteps}
      name={translations["widget-CCvsYou-CC-name"]}
      place={userFirst ? 2 : 1}
      steps={ccValue}
    />
  );

  const yourBar = (
    <VerticalBar
      key={'user' + selectedTime}
      mainColor={YOU_COLOR}
      maxSteps={maxSteps}
      name={translations["widget-CCvsYou-You-name"]}
      place={userFirst ? 1 : 2}
      steps={userValue}
    />
  );

  return (
    <div className={styles.wrapper}>
      <div className={styles.mainContent}>
        <div className={styles.left}>
          {userFirst && yourBar}
          {!userFirst && ccBar}
        </div>
        <div className={styles.center}>vs</div>
        <div className={styles.right}>
          {!userFirst && yourBar}
          {userFirst && ccBar}
        </div>
      </div>
      <div className={styles.downPart}>
        <div className="ui bottom attached buttons">
          <button
            className={`${styles.ui} ${styles.firstButton} ${styles.button} ${selectedTime === 'day' ? styles.buttonSelected : ''}`}
            onClick={() => setSelectedTime('day')}
          >
            {translations['widget-day']}
          </button>
          <button
            className={`${styles.ui} ${styles.button} ${selectedTime === 'week' ? styles.buttonSelected : ''}`}
            onClick={() => setSelectedTime('week')}
          >
            {translations['widget-week']}
          </button>
          <button className={`${styles.ui} ${styles.lastButton} ${styles.button} ${selectedTime === 'month' ? styles.buttonSelected : ''}`}
            onClick={() => setSelectedTime('month')}
          >
            {translations['widget-month']}
          </button>
        </div>
      </div>
    </div>
  )
} 
