import React from 'react'

import { useSpring, animated } from 'react-spring'

import { ReactComponent as IconRun } from '../assets/run_man.svg';
import { ReactComponent as MedalGold } from '../assets/ic_medal_gold.svg';
import { ReactComponent as MedalSilver } from '../assets/ic_medal_silver.svg';
import { ReactComponent as MedalBronze } from '../assets/ic_medal_bronze.svg';

import styles from './HorizontalBar.module.css';

// added to the mainColor hex to make it transparent
// used in the background of the "not filled steps"
const ALPHA_BACKGROUND = '24';
const MAX_BAR_LENGTH_FRACTION = 0.95;

export const HorizontalBar = (props) => {
  const {
    className = '',
    mainColor,
    marked,
    maxSteps,
    name,
    place,
    steps,
  } = props;

  const medalProps = { style: { marginTop: 3 } };
  let medal = '';
  if (place === 1) {
    medal = <MedalGold {...medalProps} />;
  } else if (place === 2) {
    medal = <MedalSilver {...medalProps} />;
  } else if (place === 3) {
    medal = <MedalBronze {...medalProps} />;
  }

  const relativeLength = steps / maxSteps;

  let tooSmall = false;
  if (relativeLength < 0.10) {
    tooSmall = true;
  }

  const finalFlexBasis = relativeLength * MAX_BAR_LENGTH_FRACTION * 100;

  const animProps = useSpring({ flexBasis: finalFlexBasis + '%', from: { flexBasis: '0%' } })

  const barFilledStyle = {
    background: `linear-gradient(90deg, ${mainColor}, ${mainColor}7a)`,
    ...animProps,
  }

  return (
    <div className={`${styles.barWrapper} ${marked ? styles.markedRow : ''} ${className}`}>
      <div className={styles.rank}>
        {place}
      </div>
      <div className={styles.medal}>
        {medal}
      </div>
      <div className={styles.name}>
        {name}
      </div>
      <div className={styles.fullBar} style={{ backgroundColor: mainColor + ALPHA_BACKGROUND }}>
        <animated.div className={styles.barFilled} style={barFilledStyle}>
          {!tooSmall && steps}
        </animated.div>
        <div className={styles.barRest}>
          {tooSmall && <div className={styles.stepsTooSmall}>{steps}</div>}
          <IconRun height={19} width={19} style={{ marginTop: 2, fill: mainColor }} />
        </div>
      </div>
    </div>
  )
}
