import React from 'react';
import { Dropdown, Header } from 'semantic-ui-react'


export const CompetitionSelect = (props) => {


  const { competitions, translations, dropdownSelected } = props;

  const options = competitions



  options.push({
    text: translations['widget-CCvsYou'],
    value: "CCvsYou",
    content: <Header content={translations['widget-CCvsYou']} color="teal" size="small" />,
  })

  const filteredOptions = options.map(o => ({
    text: o.text,
    value: o.value,
    key: o.key,
  }));
  return (
    <Header as='h4'>
      <Dropdown
        onChange={props.handleChange}
        value={dropdownSelected}
        options={filteredOptions}
      >
      </Dropdown>
    </Header>
  );
}
