import React, { useState } from 'react';
import { formatRelative } from 'date-fns'
import { de, enUS } from 'date-fns/locale'

import 'semantic-ui-css/semantic.min.css'

import styles from './App.module.css';

import { CompetitionSelect } from './components/CompetitionSelect';
import { HorizontalBar } from './components/HorizontalBar';
import { CCvsYou } from './components/CCvsYou';


const sortCompetitions = (a, b) => {
  if (a.finished && !b.finished) return 1;
  return a.end <= b.end ? 1 : -1;
}

function App() {
  // get injected variables
  const result = window.result;
  const translations = window.translations;
  const actionId = window.actionId;
  // console.log(actionId); // eslint-disable-line
  const mydaco = window.mydaco;
  // console.log(mydaco);
  const localeKey = window.localeKey;
  const dropdownExternalInit = window.dropdownExternalInit;

  const allCompetitions = [];

  result.competitionValues.competitions.running.forEach(c => {
    allCompetitions.push({
      ...c,
      finished: false,
      start: new Date(c.start),
      end: new Date(c.end),
      value: c.id,
      key: c.id,
      content: c.competitionName,
      text: c.competitionName,
    })
  })

  result.competitionValues.competitions.finished.forEach(c => {
    allCompetitions.push({
      ...c,
      finished: true,
      start: new Date(c.start),
      end: new Date(c.end),
      value: c.id,
      key: c.id,
      content: c.competitionName,
      text: c.competitionName,
    })
  })

  allCompetitions.sort(sortCompetitions);

  let initDropdown = null;
  let selectedCompetition = null;
  if (dropdownExternalInit !== '') {
    initDropdown = dropdownExternalInit;
  } else if (allCompetitions.length > 0) {
    initDropdown = allCompetitions[0].value;
  } else {
    initDropdown = 'CCvsYou';
  }
  const [dropdownSelected, setDropdownSelected] = useState(initDropdown);

  let subDropdown = '-'
  if (dropdownSelected !== null && dropdownSelected !== 'CCvsYou') {
    selectedCompetition = allCompetitions.find(c => c.id === dropdownSelected);

    subDropdown = 'Ends: ' + selectedCompetition.end.toLocaleDateString('de-DE', {
      year: 'numeric',
      month: '2-digit',
      day: '2-digit',
      hour: 'numeric',
      minute: '2-digit',
    });
  }


  let resultBars = [];
  let userUpdateTime;

  if (selectedCompetition && selectedCompetition.steps) {
    // console.log(selectedCompetition);
    const resultArray = [];
    Object.entries(selectedCompetition.steps).forEach(([name, value]) => {
      resultArray.push({
        name,
        time: value.time,
        steps: value.steps,
      });
    });

    resultArray.sort((a, b) => a.steps < b.steps ? 1 : -1);

    let userIsIn = false;
    let maxSteps = 1; // just in case I stupidly divide later
    const resultsShown = [];
    resultArray.forEach((entry, index) => {
      if (index === 0) {
        maxSteps = entry.steps;
      }

      if (index <= 3 || (index === 4 && userIsIn)) {
        resultsShown.push({ ...entry, place: index + 1 });
      }

      if (entry.name === selectedCompetition.personName) {
        userIsIn = true;
        userUpdateTime = new Date(entry.time);
        if (index >= 4) {
          // always include the user
          resultsShown.push({ ...entry, place: index + 1 });
        }
      }
    })

    resultBars = resultsShown.map((entry) => (<HorizontalBar
      className={styles.barMargin}
      place={entry.place}
      mainColor={selectedCompetition.colors[entry.name] || '#cccccc'}
      name={entry.name}
      key={`${selectedCompetition.id}-${entry.name}`}
      marked={selectedCompetition.personName === entry.name}
      steps={entry.steps}
      maxSteps={maxSteps}
    />));
  }


  if (dropdownSelected === 'CCvsYou') {
    subDropdown = translations['widget-CCvsYou-subtitle'];
  }

  if (mydaco) { // eslint-disable-line
    let locale = enUS;
    let clockName = ''
    if (localeKey === 'de') {
      locale = de;
      clockName = ' Uhr';
    }
    let subTitleTime;
    if (dropdownSelected === 'CCvsYou') {
      subTitleTime = formatRelative(new Date(), new Date(), { locale }) + clockName;
    } else {
      // console.log(userUpdateTime);
      if (userUpdateTime) {
        subTitleTime = formatRelative(userUpdateTime, new Date(), { locale }) + clockName;
      } else {
        subTitleTime = '';
      }
    }
    // console.log(subTitleTime);
    mydaco.setSubTitle(translations['widget-last-updated'] + subTitleTime, actionId) // eslint-disable-line
  }


  const handleDropdownChange = async (e, { value }) => {
    setDropdownSelected(value);
    if (mydaco) { // eslint-disable-line
      await mydaco.interface('KeyValueStore', 'put', { key: '_component_widget_selection', value: value }) // eslint-disable-line
    }
  };

  return (
    <div className={styles.App}>
      <div className={styles.competitionSelect}>
        <CompetitionSelect
          competitions={allCompetitions}
          translations={translations}
          dropdownSelected={dropdownSelected}
          handleChange={handleDropdownChange}
        />
      </div>
      <div className={styles.subDropdown}>
        {subDropdown}
      </div>
      <div className={styles.appContent}>
        {dropdownSelected !== 'CCvsYou' && (
          <div className={styles.barsDiv}>{resultBars}</div>
        )}
        {dropdownSelected === 'CCvsYou' && (
          <CCvsYou
            data={result.nonCompetitionValues}
            translations={translations}
          />
        )}
      </div>
    </div>
  );
}

export default App;
